html {
  box-sizing: border-box;
  font-family: Roboto, Arial, sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo-img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav {
  unicode-bidi: isolate;
  display: block;
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-item {
  margin-right: 20px;
}

.last-item {
  margin-right: 0;
}

.nav-link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 60px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.is-active, .nav-link:hover {
  color: #00acdc;
}
